<template>
	<div class="app-content">
		<!-- #ifdef APP-PLUS || MP -->
		<view class="app-statusBar">
			<view class="top_view"></view> 
		</view>  
		<!-- #endif --> 
		<div class="content login animated fadeIn"> 
			<view  class="login-header">
				<div class="loginheader-item">
					<span>API</span>
					<image class="loginGear" src="../../static/images/loginGear.png" @tap="selectServer"></image>
					<image class="logoT" src="../../static/images/logoT.png"></image>
				</div>
				<div class="loginheader-text">i4.0 SOLUTIONS</div>
			</view>
			<div class="login-content">
				<view class="login-item">
					<m-input-lable class="m-input" :element="userElemnt" ref="username" sourceType="form"
					 :value="formData.username"
					 @changeValue = "changeValue"></m-input-lable>
				</view>
				<view class="login-item">
					<m-input-lable :element="passworderElemnt" sourceType="form" ref="password"
					 :value="formData.password"
					 :showPassword = "isShowPassword"
					 @changeValue = "changeValue">
						<m-font-icon slot="btns" :icon="passwordIcon" :iconSize="48" @click="showPassword"></m-font-icon>
					</m-input-lable>
				</view>
				<div class="login-item login-content-footer">
					<!-- <view class="password-forget">
						<m-button :btnTilte="$i18nMsg('lang-forget')" :isFontBtn="true" :fontSize="28" @elementClick="forgetPassword"></m-button>
					</view> -->
					<view class="login-remb" :class="{remeber:remeber}">
						<m-button :isFontBtn="true" :fontSize="28" @elementClick="rememberPassword">
							<block slot="buttonIcon" style="display: inline-block;">
								<m-font-icon icon="iconjizhumimaxuanzhong" :iconSize="28"></m-font-icon>
								<text>{{ $i18nMsg('lang-remember') }}</text>
							</block>
						</m-button>
					</view>
				</div>
			</div>
			<view class="login-foot">
				<!-- 中文：登录 -->
				<m-button ref="loginBtn" :disabled="disabled" :isLoading="isLoading" :btnTilte="$i18nMsg('lang-sign-in')" btnClass="mainColor" @elementClick="doLogin"></m-button>
			</view>
		</div> 
	</div>
</template>

<script>
	import { mapState, mapMutations } from 'vuex'
	import mInputLable from '../../components/element/m-inputLable1.vue'
	import {homeApi} from '@/common/request/api.js'
	
	import EncryptionPSW from '@/static/js/encryptionPSW.js'
	const remberUser = uni.getStorageSync('loginMsg') || {}
	export default { 
		name:"Login",
		components: {mInputLable},
		data() {
			return {
				userElemnt:{visiabled:1,labletPositon:"top",label:this.$i18nMsg('lang-username'),name:"username"},//中文："账号"
				passworderElemnt:{visiabled:1,labletPositon:"top",label:this.$i18nMsg('lang-password'),name:"password","inputType":'password'},//中文："密码"
				remeber:true,//默认记住密码
				formData:remberUser,
				PasswrodEncrypted:"0",
				disabled:true,
				isLoading:true,
				passwordIcon:'iconmimayincang',
				isShowPassword:true
			}
		},
		methods:{
			...mapMutations(["SET_LOGIN_STATE","SET_SYSPARAMER","SET_WATERMARK"]),
			showPassword(){//点击icon
				if( this.isShowPassword){
					this.passwordIcon = 'iconmimaxianshi'
					this.isShowPassword = false
				}else {
					this.passwordIcon = 'iconmimayincang'
					this.isShowPassword = true
				}
			},
			changeValue(param){
				this.formData[param.element.name] = param.value
			},
			//忘记密码
			forgetPassword() {
				let locale = this._i18n.locale === 'en_US' ? 'zh_CN' : 'en_US'
				uni.setStorageSync("locale",locale)
				plus.runtime.restart();  
			},
			//记住密码
			rememberPassword() {this.remeber = this.remeber ? false : true},
			selectServer(){
				uni.navigateTo({
					url:"selectServer"
				})
			},
			//登录
			doLogin () {
				const _t = this
				_t.isLoading=true
				_t.disabled=true
				setTimeout(function(){
					//是否校验通过
					const checkRes = _t.Static_GlobalFucs.checkForm({
						rule:[
							{name:"username", checkType : "notnull", checkRule:"",  errorMsg:_t.$i18nMsg('lang-username') + _t.$i18nMsg('lang-notNull')},//中文："用户不能为空"
							{name:"password", checkType : "notnull", checkRule:"",  errorMsg:_t.$i18nMsg('lang-password') + _t.$i18nMsg('lang-notNull')}//中文："密码不能为空"
						],
						data:_t.formData
					})
					if(!checkRes.isCheck){
						_t.Static_GlobalFucs.showToast({title:checkRes.errorMsg})
						_t.isLoading=false
						_t.disabled=false
						return 
					}
					
					//是否需要记忆密码
					if(_t.remeber) uni.setStorageSync('loginMsg', _t.formData)
					else uni.removeStorageSync('loginMsg')
					
					let encyData = EncryptionPSW(_t.formData,_t.PasswrodEncrypted)
					encyData.language=_t._i18n.locale //记录语言
					homeApi.getToken(_t, encyData).finally(()=>{
						_t.isLoading=false
						_t.disabled=false
					})
				}, 50)
			},
			getSysParamer(params){
				return homeApi.sysParamer(this, params)
			},
			saveWaterMarkConfig(params){
				this.$http.get('variable/getValue', {variableName: 'WaterMarkConfig'})
				.then(res => {
					const result = res.data.data
					this.SET_WATERMARK(result)
				})
				.catch(err => {
					console.log(JSON.stringify(err))
				});
			}
		},
		mounted() {
			let formData = uni.getStorageSync('loginMsg') || {}
			if(JSON.stringify(formData) !== "{}"){
				this.remeber = true
			}
			
			const _this = this
			Promise.all([this.getSysParamer("PasswrodEncrypted")]).then((result) => {
				let isFail = result.find(item => !item || item.success === false) || ""
				if(isFail) {
					throw isFail
					return 
				}
				for(let i = 0,len = result.length;i<len;i++){
					_this.PasswrodEncrypted = result[i].data
				}
				_this.isLoading=false
				_this.disabled=false
			}).catch((error) => {
				this.Static_GlobalFucs.showModal(this,{content:JSON.stringify(error)},"error")
			})
			// this.$Toast.showLoading()
			this.saveWaterMarkConfig('WaterMarkConfig')
		},
		onLoad(){
			this.SET_LOGIN_STATE({})
		}
	}
</script>